import React, { useContext } from "react";
import styled from "styled-components";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink,
} from "react-router-dom";
import CheckoutListItem from "components/organisms/CheckoutListItem/CheckoutListItem";
import { StoreContext } from "providers/StoreProvider";
import ShoppingCartElement from "components/organisms/ShoppingCartElement/ShoppingCartElement";
import ShippingPage from "components/organisms/ShippingPage/ShippingPage";

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: whitesmoke;
  flex-direction: column;
`;

const ProgressBar = styled.div`
  width: 30%;
  height: 10%;
  margin: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
`;

const RoundButton = styled(NavLink).attrs({ activeClassName: "active-link" })`
  display: flex;
  border: none;
  box-shadow: -2px 4px 10px lightgrey;

  width: 120px;
  height: 120px;
  margin: 30px;
  border-radius: 50%;
  background: none;
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: black;
  align-items: center;
  transition: all 0.2s ease-in-out;
  &:hover {
    width: 140px;
    height: 140px;
    margin: 20px;
  }
  &.active-link {
    width: 150px;
    height: 150px;
    margin: 15px;
    background-color: #ffd1dc;
    &:hover {
      width: 160px;
      height: 160px;
      margin: 10px;
    }
  }
`;

const ContentFrame = styled.div`
  width: 100%;
  padding: 0 0 0 0;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;

  @media (min-width: 1024px) {
    width: 50%;
    flex-wrap: wrap;
    padding: 0 0 0 0;
    height: 70%;
  }

  .element {
    width: 100vw;
    height: 40vh;
    margin: 20px;
    .size {
      background-color: yellow;
    }
    @media (min-width: 1024px) {
      width: 35%;
      padding-right: 15px;

      height: 25vh;
      min-height: 20vh;
      margin: 20px;
    }
  }
`;

const Overview = styled.div`
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  flex-wrap: wrap;
`;

const ContinueButton = styled.button`
  width: 200px;
  height: 50px;
  background-color: #77dd77;
  border-radius: 20px;
  border: none;
`;

const CheckoutPage = () => {
  const { cart } = useContext(StoreContext);
  const CheckIsEmpty = () => {
    if (Object.keys(cart).length != 0) {
      return cart.map((productData) => (
        <CheckoutListItem productData={productData} />
      ));
    } else {
      return <h1>Your cart is empty, add something</h1>;
    }
  };
  return (
    <Router>
      <Wrapper>
        <ProgressBar>
          <RoundButton to="/checkout/overview">Overview</RoundButton>
          <RoundButton to="/checkout/shipping">Shipping</RoundButton>
          <RoundButton to="/checkout/payment">Payment</RoundButton>
        </ProgressBar>

        <h3> Your cart: </h3>

        <ContentFrame>
          <Route path="/checkout/overview">
            <Overview>
              <CheckIsEmpty />
            </Overview>
            <Link to="/checkout/shipping" exact>
              <ContinueButton className="continue">
                Proceed to shipping
              </ContinueButton>
            </Link>
          </Route>

          <Route path="/checkout/shipping">
            <Overview>
              <ShippingPage />
            </Overview>
            <Link to="/checkout/payment" exact>
              <ContinueButton className="continue">
                Proceed to payment
              </ContinueButton>
            </Link>
          </Route>

          <Route path="/checkout/payment">
            <Overview>
              <h1>Payment</h1>
            </Overview>
            <Link to="/checkout/payment" exact>
              <ContinueButton className="continue">Pay now.</ContinueButton>
            </Link>
          </Route>
        </ContentFrame>
      </Wrapper>
    </Router>
  );
};

export default CheckoutPage;
